<template>
  <div class="content">
    <el-tabs type="card" @tab-click="toggleTab">
      <el-tab-pane>
        <span slot="label"><i class="el-icon-date"></i> 添加会议</span>
        <el-form
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="会议名称" prop="title">
            <el-input v-model="ruleForm.title" style="width: 54%"></el-input>
          </el-form-item>
          <el-form-item label="开始时间" required>
            <el-col :span="6">
              <el-form-item prop="date1">
                <el-date-picker
                  type="date"
                  placeholder="选择日期"
                  v-model="ruleForm.date1"
                  style="width: 100%"
                  :picker-options="{
                    disabledDate(time) {
                      return time.getTime() < Date.now() - 8.64e7;
                    },
                  }"
                ></el-date-picker>
              </el-form-item>
            </el-col>
            <el-col :span="1" style="text-align: center">-</el-col>
            <el-col :span="6">
              <el-form-item prop="date2">
                <el-time-picker
                  placeholder="选择时间"
                  v-model="ruleForm.date2"
                  style="width: 100%"
                ></el-time-picker>
              </el-form-item>
            </el-col>
          </el-form-item>
          <el-form-item label="会议地点" prop="place">
            <el-input v-model="ruleForm.place" style="width: 54%"></el-input>
          </el-form-item>
          <el-form-item label="参会人员" prop="attendee">
            <el-input v-model="ruleForm.attendee" style="width: 54%"></el-input>
          </el-form-item>
          <el-form-item label="会议内容" prop="content">
            <el-input
              type="textarea"
              v-model="ruleForm.content"
              style="width: 60%"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"
              >立即创建</el-button
            >
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane>
        <span slot="label"><i class="el-icon-date"></i> 删除会议</span>
        <el-card v-for="item in meetingInfo" :key="item.id">
          <el-row style="text-align: center">
            <span :title="item.title"
              ><strong>{{ item.title }}</strong></span
            >
            <el-button type="danger" @click="deleteMeeting(item.id)"
              >删除</el-button
            >
          </el-row>
          <el-row>时间：{{ item.time }}</el-row>
          <el-row :title="item.place">地点：{{ item.place }}</el-row>
          <el-row :title="item.attendee">参会人员：{{ item.attendee }}</el-row>
          <div class="meeting-content">会议内容：{{ item.content }}</div>
        </el-card>
        <el-empty description="暂无会议" v-if="!meetingInfo.length"> </el-empty>
      </el-tab-pane>
      <el-tab-pane>
        <span slot="label"><i class="el-icon-view"></i> 消息中心</span>

        <el-form
          style="margin-left: 15%"
          :model="ruleForm2"
          :rules="rules2"
          ref="ruleForm2"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="消息内容" prop="content">
            <el-input
              type="textarea"
              v-model="ruleForm2.content"
              style="width: 60%"
            ></el-input>
            <el-button
              type="primary"
              @click="submitForm2('ruleForm2')"
              class="ml-10"
              >立即创建</el-button
            >
            <el-button @click="resetForm2('ruleForm2')">重置</el-button>
          </el-form-item>
        </el-form>

        <div class="msg-box">
          <el-card
            style="width: 45%; min-height: 60px"
            v-for="(item, index) in messageInfo"
            :key="index"
          >
            <div>
              {{ item.content }}
              <i
                class="el-icon-close close"
                @click="deleteMessageInfo(index)"
              ></i>
            </div>
          </el-card>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { addMeeting, getAllmeeting, deletemeeting } from '@/api/meeting'
import { deepClone } from '@/utils/index.js'
export default {
  data () {
    return {
      ruleForm: {
        title: '',
        date1: '',
        date2: '',
        place: '',
        attendee: '',
        content: ''
      },
      ruleForm2: {
        content: ''
      },
      rules: {
        title: [{ required: true, message: '请输入会议名称', trigger: 'blur' }],
        date1: [
          {
            type: 'date',
            required: true,
            message: '请选择日期',
            trigger: 'change'
          }
        ],
        date2: [
          {
            type: 'date',
            required: true,
            message: '请选择时间',
            trigger: 'change'
          }
        ],
        place: [{ required: true, message: '请输入会议地点', trigger: 'blur' }],
        attendee: [
          { required: true, message: '请输入参会人员', trigger: 'change' }
        ]
      },
      meetingInfo: [],
      // messageInfo: this.$store.state.user.messageInfo,
      // messageInfo: [],
      messageInfo: localStorage.getItem('messageInfos')
        ? JSON.parse(localStorage.getItem('messageInfos'))
        : [],
      rules2: {
        content: [
          { required: true, message: '请输入消息内容', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 切换tab
    toggleTab () {
      this.$nextTick(() => {
        // 删除会议
        const flag =
          +document.getElementById('tab-1').getAttribute('tabindex') + 1
        if (flag) {
          this.getAllMeeting()
        }
      })
    },
    // 添加会议
    submitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const date1 = new Date(this.ruleForm.date1)
          const year = date1.getFullYear()
          const month = (date1.getMonth() + 1).toString().padStart(2, '0')
          const day = date1.getDate().toString().padStart(2, '0')
          const date2 = new Date(this.ruleForm.date2)
          const hour = date2.getHours().toString().padStart(2, '0')
          const min = date2.getMinutes().toString().padStart(2, '0')
          const sec = date2.getSeconds().toString().padStart(2, '0')
          const time = year + '/' + month + '/' + day + ' ' + hour + ':' + min
          this.ruleForm.time = time
          this.ruleForm.orderTime = year + month + day + hour + min + sec
          await addMeeting(this.ruleForm)
          this.$message.success('创建成功!')
          this.resetForm('ruleForm')
        } else {
          return false
        }
      })
    },
    // 重置
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    resetForm2 (formName) {
      this.$refs[formName].resetFields()
    },
    deleteMessageInfo (index) {
      this.$confirm('确定删除该消息吗 ?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          const reslut = JSON.parse(
            localStorage.getItem('messageInfos')
          ).filter((item, idx) => idx !== index)
          localStorage.setItem('messageInfos', JSON.stringify(reslut))
          this.messageInfo.splice(index, 1)
          if (!reslut.length) localStorage.removeItem('messageInfos')
          this.$message.success('删除成功')
        })
        .catch((err) => err)
    },
    submitForm2 (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.messageInfo.push(deepClone(this.ruleForm2))
          // console.log(this.ruleForm2);
          localStorage.setItem(
            'messageInfos',
            JSON.stringify(this.messageInfo)
          )
          // this.$store.commit("setMessageInfo", deepClone(this.ruleForm2));
          this.$message.success('创建成功')
          this.ruleForm2.content = ''
        } else {
          return false
        }
      })
    },
    // 获取会议
    async getAllMeeting () {
      this.meetingInfo = await getAllmeeting()
    },
    // 删除会议
    deleteMeeting (id) {
      this.$confirm('确定删除该会议吗 ?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await deletemeeting(id)
          this.getAllMeeting()
          this.$message.success('已删除')
        })
        .catch((res) => res)
    }
  }
}
</script>

