import request from '@/utils/request'
// 添加会议
export function addMeeting (data) {
  return request({
    url: '/meeting/add',
    method: 'post',
    data: data
  })
}
// 全部会议
export function getAllmeeting () {
  return request({
    url: '/meeting/allmeeting',
    method: 'get'
  })
}
// 删除会议
export function deletemeeting (id) {
  return request({
    url: '/meeting/delete?id=' + id,
    method: 'get'
  })
}
